header {
    width: 100%;
    margin-bottom: 10px;
}

p.heading {
    padding: 25px 0px;
    font-size: 35px;
    font-weight: 700;
    color: white;
    background-color: #17191d;
}

@media screen and (max-width: 600px) {
    p.heading {
        font-size: 25px;
        padding: 15px 0px;
    }
}
